import React, { useEffect, useContext, useState } from 'react';
import { Col, Row, Container, Card, Jumbotron } from "react-bootstrap";
import { Link } from 'react-router-dom'
import Helmet from "react-helmet";
import Face from "../../img/face.png";
import { SocialContext, SocialProvider } from '../../context/socialContext';
import { FaRegTimesCircle } from "react-icons/fa";
import "../user/profile.css";
import axios from 'axios';
import { Fade } from "react-reveal"
import { toast } from 'react-toastify';
const Profile = (props) => {
   const { user, token, auth, post } = useContext(SocialContext);
   const [userValue, setUserValue] = user;
   const [tokenValue, setTokenValue] = token;
   const [status, setStatus] = useState("");
   const [userPost, setUserPost] = useState([])
   const[userName , setUserName] = useState('');
   const[userStat , setUserStat] = useState('')
   const[userImg , setUserImg] = useState('')
   const[count , setCount] = useState('')
   const[name, setName] = useState('')

   const [showEdit, setShowEdit] = useState(false)
   useEffect(() => {
      const userId = props.match.params.userId;
      axios.get(`https://api.maniyeganeh.ir/auth/${userId}`)
         .then(res => {
            
 
               
            setUserName(res.data.user.name)
            setUserStat(res.data.user.status)
            setUserImg(res.data.user.profilePic)
            setUserPost(res.data.user.posts);
            setCount(res.data.user.posts.length);
            

         }).catch(err => {
            console.log(err);

         })
      
         


      
         
       
     
       
         

   }, [props.match.params.userId], [userValue])
 

   const submitPic = (postData) => {
      postData.preventDefault()
      const userID = props.match.params.userId;
      const formData = new FormData();
      formData.append('name', name)
      formData.append('status', status)
      formData.append('image', postData.target.profilePic.files[0]);
      fetch("https://api.maniyeganeh.ir/auth/" + userID, {
         method: "PUT",
         body: formData,
         headers: {
            Authorization: 'Bearer ' + localStorage.getItem('Your_token')
         }
      }).then(res => {

         return res.json()

      }).then(resData => {

         
         setUserValue(resData.user)
         localStorage.setItem('user' , resData.user.name)
         localStorage.setItem('userPic' , resData.user.profilePic)
         toast.success("Profile Updated" , {
            position:"top-right"
         })

      }).catch(err => {
         console.log(err);

      })
      handleShow()
   }
   const handleShow = () => {
      setShowEdit(!showEdit)
   }
   const statusChange = (e) => {
      setStatus(e.target.value)




   }
   const nameChange = (e) => {
      setName(e.target.value)




   }
   return (
      <>

         <Helmet>
            <title>
               Social | Profile
           </title>
         </Helmet>
         <SocialProvider >
            <Container fluid className={showEdit || props.darkMode ? "userPro editOp darkRow" : "userPro"}>
               <Row >
                  
                  {localStorage.getItem('userId')=== props.match.params.userId ? <Col xs={12} sm={12} md={12}>
                     <Fade>
                     <Jumbotron className="text-center jumb">
                        <div className="userImg">
                           {(userImg === undefined) ?
                              <img src={Face} className={props.darkMode ? "proDark" : ""} alt="face" />
                              :
                              <img src={`https://api.maniyeganeh.ir/${userImg}`} alt="pro" />
                           }


                        </div>
                        <h2>
                           {userName}
                        </h2>
                        <button className="btn btn-sm btn-dark" onClick={handleShow}>
                           Edit Profile
                        </button>
                        <h3>
                           {userStat}
                        </h3>
                           <h3>
                             Posts: {count}
                           </h3>
                     </Jumbotron>
                     </Fade>

                  </Col>
                     :
                     <>
                        <Col xs={12} sm={12} md={12}  style={{marginTop:"0%"}}>
                           <Fade>
                        <Jumbotron className="text-center jumb" >
                              <div className="userImg">
                              {userImg === undefined || userImg === null  || userImg === " " ?
                              <img src={Face}  className={props.darkMode ? "proDark" : ""} alt="face" />
                              :
                              <img src={`https://api.maniyeganeh.ir/${userImg}`} alt="pro" />
                           }


                              </div>
                              <h2>
                                 {userName}
                              </h2>
           
                              <h3>
                                 {userStat}
                              </h3>
                              <h3> Posts : {count}</h3>
                           
                           </Jumbotron>
                           </Fade>
                        {/* {postData.map((pst,index ) =>(
                           
                           pst.creator._id === props.match.params.userId ? 
                          
                           <Jumbotron className="text-center jumb" >
                              <div className="userImg">
                              {(pst.creator.profilePic === null || pst.creator.profilePic === " " || pst.creator.profilePic === undefined) ?
                              <img src={Face} alt="face" />
                              :
                              <img src={`https://api.maniyeganeh.ir/${pst.creator.profilePic}`} alt="pro" />
                           }


                              </div>
                              <h2>
                                 {pst.creator.name}
                              </h2>
           
                              <h3>
                                 {pst.creator.status}
                              </h3>

                           </Jumbotron>
                         
                       
                        :null
                        )
                           
                        )} */}
                       </Col>

                     </>
                  }

               </Row>

             
               <Container>

                  <div className="userflex">

                     {userPost.map((post, index) => (


                        <div className="userBx" key={index} style={{ backgroundImage: `url(https://api.maniyeganeh.ir/${post.imageUrl})` }}>
                           <Link to={`/post/${post._id}`}>
                              <div>

                                 <h5>
                                    {post.title}
                                 </h5>


                              </div>
                           </Link>
                        </div>

                     ))}
                  </div>

           
               </Container>
               {showEdit ?
                  <Card className={props.darkMode ? "editCard editDark" : "editCard"}>
                     <Card.Body>
                        <div className={props.darkMode ? "closeEdt closeDark" : "closeEdt"} onClick={handleShow}>
                           <FaRegTimesCircle />
                        </div>
                        <form className="editForm" onSubmit={submitPic}>
                        <div className="form-group">
                              <label htmlFor="name">Edit Your Name</label>
                              <input type="text" id="name" name="name" className="form-control" value={name} onChange={nameChange} />

                           </div>
                           <div className="form-group">
                              <label htmlFor="status">Edit Your Status</label>
                              <input type="text" id="status" name="status" className="form-control" value={status} onChange={statusChange} />

                           </div>
                           <div className="form-group " className="file">
                              <label className="form-check-label" htmlFor="image">Your Image <span>(JPEG , JPG , PNG)</span> </label>

                              <input type="file" id="profilePic" name="profilePic" className="form-check-input" />

                           </div>
                           <div className="sub">
                              <button type="submit" className="btn btn-success">Submit</button>

                           </div>
                        </form>
                     </Card.Body>
                  </Card> : null}

            </Container>

         </SocialProvider>
      </>
   );
}

export default Profile;