import React, { Fragment, useState, useEffect, useContext } from 'react'
import { FaHamburger, FaTimes } from "react-icons/fa";
import { NavLink, Link } from "react-router-dom"
import { menuItems } from "../Nav/menuItems";
import { SocialProvider } from '../../context/socialContext';
import { WiDaySunny, WiMoonAltThirdQuarter } from "react-icons/wi";
const Hamburger = ({ user, auth, token, logoutHandler, darkModeHandle, darkMode }) => {

  const [showMenu, SetShowMenu] = useState(false);
  const handleShowMenu = () => {
    SetShowMenu(!showMenu)
  }

  return (
    <SocialProvider>


      <Fragment>
        <FaHamburger className="ham" onClick={handleShowMenu}
          style={{
            color: "#FBFBFB",
            fontSize: "30px",

          }}
        />


        <nav className={showMenu ? 'nav-menu active' : 'nav-menu'}>
          <ul className='nav-menu-items' onClick={handleShowMenu}>
            <li className='navbar-toggle'>
              <div className='menu-bars' style={{
                textAlign: "center"
              }}>
                <FaTimes />
              </div>
            </li>
            {localStorage.getItem("Your_token") !== null ?
              <>
                <li style={{
                  marginTop: "50%",
                  width: "100%"
                }}>
                  
                  <Link exact to={`/user/${localStorage.getItem('userId')}`} className="proLi" activeStyle={{
                    background: "#F93154",
                    color: "white",
                    borderRadius: "20px",
                    width: "100%",
                    padding: "10px 30px",

                  }} >
                    Profile
            </Link>
                </li>
              </>
              :
              null

            }
            {menuItems.map((item, index) => {
              return (
                <Fragment key={index} >
                  <li className={item.csName}>
                    <NavLink exact to={item.to} className="links" activeStyle={{
                      background: "#F93154",
                      color: "white",
                      borderRadius: "20px"
                    }}>
                      {item.title}
                    </NavLink>
                  </li>

                </Fragment>
              );
            })}
            {localStorage.getItem('Your_token') !== null ?
              <li className="menu-Links">
                <NavLink to="/login" className="logLi" activeStyle={{
                  background: "#F93154",
                  color: "white",
                  borderRadius: "20px",
                  width: "100%",
                  padding: "10px 30px",


                }} onClick={logoutHandler}>
                  Logout
                      </NavLink>
              </li> :

              <li className="menu-Links">
                <NavLink exact to="/login" className="logLi" activeStyle={{
                  background: "#F93154",
                  color: "white",
                  borderRadius: "20px",
                  width: "100%",
                  padding: "10px 30px"
                }}>
                  Login
                    </NavLink>
              </li>
            }
             <li className="menu-Links" onClick={darkModeHandle}>
              {darkMode ? 
                <button className="btn btn-dark">
                  Go Light
                </button>
                :
                <button className="btn btn-light">
                  Go Dark
                </button>
                }
            </li> 
          </ul>
        </nav>


      </Fragment>
    </SocialProvider>
  );
}

export default Hamburger;