import React, { useState, useEffect, useContext , useCallback } from 'react'
import { Container, Alert, Card, Button } from "react-bootstrap";
import { Fade } from "react-reveal"
import { SocialContext, SocialProvider } from '../../context/socialContext';
import Load from "./img/30.gif"

import { FaPencilAlt } from "react-icons/fa";
import { Link } from 'react-router-dom'
import axios from 'axios'
import { toast } from 'react-toastify'
import EditModal from './EditModal';
import Face from "../../img/face.png";
import { BiSend } from "react-icons/bi";

const SinglePost = (props) => {
    const { user, token, edit } = useContext(SocialContext);
    const [userValue] = user;
    const [tokenValue] = token

    const [editValue, setEditValue] = edit;
    const [title, setTitle] = useState("")
    const [content, setContent] = useState("")
    const [image, setImage] = useState("")
    const [name, setName] = useState("")
    const [date, setDate] = useState("")
    const [id, setId] = useState("");
    const [proPic, setProPic] = useState("")
    const [loading, setLoading] = useState(false)
    const [modal, setModal] = useState(false);
    const [showCom, setShowCom] = useState(false);
    const [message, setMessage] = useState("")
    const [count, setCount] = useState(1)
    const [like, setLike] = useState('')
    const [showCm, setShowCm] = useState(false);
    const [comments, setComments] = useState([])
    const [cmName] = useState(localStorage.getItem('user'))
    const [, forceUpdate] = useState();
    const setRef = useCallback((node)=>{
       if(node){
        node.scrollIntoView({ smooth: true })
       }
        
    },[])
    useEffect(() => {


        const fetchData = async () => {
            setLoading(true)
            const postId = props.match.params.postId;
            try {
                const res = await axios(`https://api.maniyeganeh.ir/feed/post/${postId}`)
                setTitle(res.data.post.title)
                setContent(res.data.post.content)
                setName(res.data.post.creator.name)
                setProPic(res.data.post.creator.profilePic)
                setId(res.data.post.creator._id)
                setLike(res.data.post.likes)
                setDate(new Date(res.data.post.createdAt).toLocaleString('uk', { hour12: false }))
                setImage("https://api.maniyeganeh.ir/" + res.data.post.imageUrl)
                setLoading(false)
              

                

            } catch (err) {
                console.log(err);
                toast.error("Something Went Wrong", {
                    position: "top-right"
                })
                setLoading(false)

            }
        }
        fetchData()



    }, [props.match.params.postId])
    // const validator = useRef(
    //     new SimpleReactValidator({
    //         messages: {
    //             required: "You Should Fill All the Fields",
    //             min: "5 Character at Least",

    //         },
    //         element: message => <div style={{
    //             background: "#c54848",

    //             padding: "10px 10px",
    //             color: "white",
    //             marginTop: "2%",
    //             borderRadius: "5px"
    //         }}>

    //             {message}

    //         </div>
    //     })
    // )
    // const titleChange = (e) => {
    //     setTitle(e.target.value)
    //     validator.current.showMessageFor("title")


    // }
    // const contentChange = (e) => {
    //     setContent(e.target.value)
    //     validator.current.showMessageFor("content")


    // }
    useEffect(() => {
        const postId = props.match.params.postId;
        fetch('https://api.maniyeganeh.ir/feed/post/' + postId)
            .then(res => {

                return res.json()

            }).then(resData => {

                setComments(resData.post.comments)

            })
            .catch(err => {
                console.log(err);

            })

            
    }, [comments])
    const submitComment = (event) => {
        const postId = props.match.params.postId;
        event.preventDefault()
        const formData = new FormData();
        formData.append('message', message)
        formData.append('cmName', cmName)

        let url = "https://api.maniyeganeh.ir/comment/comment/" + postId
        let method = "POST";
        fetch(url, {
            method: method,
          
            body: formData,
        })
        .then(res => {
        
            if (res.status === 404) {
                toast.dark("Comment Sent Failed", {
                    position: "top-right"
                })
            } if(res.status === 201) {
          
                    toast.dark("Comment Sent Succesfully", {
                        position: "top-right"
                    })
                }
                setMessage("")
                setShowCom(false)

            }).catch(err => {
                console.log(err);

            })

    }
    const handleModal = () => {
        setModal(!modal);
        setEditValue(true)

    }
    const deletePost = () => {
        const postId = props.match.params.postId;
        fetch('https://api.maniyeganeh.ir/feed/post/' + postId, {
            method: 'DELETE',
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem("Your_token")
            }
        })
            .then(res => {
                if (res.status !== 200 && res.status !== 201) {
                    throw new Error('Deleting a post failed!');
                }
                return res.json();
            })
            .then(resData => {
                console.log(resData);
                toast.dark("Post Deleted", {
                    position: "top-right"
                })
                props.history.replace("/")
            })
            .catch(err => {
                console.log(err);
                toast.error(`${403} : This Is Not Your Post `, {
                    position: "top-right"
                })

            });
    }
    const updatePost = (event, postData) => {
        event.preventDefault();
        const postId = props.match.params.postId;
        let formData = new FormData();
        formData.append('title', title);
        formData.append('content', content);



        fetch('https://api.maniyeganeh.ir/feed/post/' + postId, {
            method: 'PUT',
            body: formData,
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('Your_token')
            }
        }).then(res => {

            return res.json();

        }).then(resData => {

            toast.dark(resData.message, {
                position: "top-right"
            })

        })

            .catch(err => {
                console.log(err);


            })

        handleModal();

        forceUpdate(1);


    }
    const commentHandle = () => {
        setShowCom(!showCom)
    }
    const commentInput = (e) => {
        setMessage(e.target.value)


    }



    const handleShowCm = () => {
        setShowCm(!showCm)
  

    }

    if (loading) {
        return (
            <div style={{ top: "45%", left: "45%", width: "100%", position: "fixed" }}>
                <img src={Load} alt="load" />
            </div>
        )
    }
    return (
        <SocialProvider>
            <Container fluid className={props.darkMode ? "darkSi" : " "} style={{
                minHeight: "250vh",
                maxHeight: "700vh",
                marginTop: "-10%",
                paddingTop: "10%",
                paddingBottom: "50px"

            }}>
                <Container>


                    {
                        title === "" ?
                            <Fade>


                                <Alert className="error" variant="danger">
                                    <Alert.Heading>
                                        Oh snap! You got an error!
             </Alert.Heading>
                                    <p>
                                        Can't Fetch The Data of This Post...!
             </p>
                                    <Link to="/" style={{
                                        padding: "10px 10px",
                                        color: "#ffff",
                                        background: "#000",
                                        borderRadius: "10px"
                                    }}>
                                        Go Back
             </Link>
                                </Alert>
                            </Fade>
                            :
                            <Fade>
                                <Card className={props.darkMode ? "single-card darkCard" : "single-card"}>
                                    <Card.Header className="card-header">
                                        {title}
                                        {localStorage.getItem('userId') === id ? <div className="edit" onClick={handleModal}>
                                            {!modal ? <FaPencilAlt /> : null}

                                        </div> : null}

                                    </Card.Header>
                                    <Card.Body className="card-content" style={{
                                        padding: "20px 10px",


                                    }}>
                                        <div className="single-img" >
                                            <Card.Img src={image} />
                                        </div>
                                        <hr />
                                        <Card.Text>
                                            {(proPic === null || proPic === " " || proPic === undefined) ?
                                                <img src={Face} className={props.darkMode ? "proFeed proDark" : "proFeed"} alt="No pic" style={{ width: "40px", height: "40px", borderRadius: "100%" }} />
                                                :
                                                <img src={"https://api.maniyeganeh.ir/" + proPic} className="proFeed" style={{ width: "40px", height: "40px", borderRadius: "100%" }} alt="Pro" />
                                            }
                                            <Link to={`/user/${id}`} className={props.darkMode ? "toLi liDark" : "toLi"}>{name}</Link>
                                            {" "} :
                        {" "} {content}

                                        </Card.Text>
                                        <hr />
                                        <Card.Text >
                                            <div className="text-center">


                                                <Button onClick={handleShowCm} >
                                                    
                                                    {showCm ? "Hide All" : "See All"} {comments.length > 1 ? `${comments.length} ${" "} Comments` : "Comments"}
                                            </Button>
                                            </div>
                                            {showCm ?
                                                <>
                                                    <div className="cm-wrapper" >
                                                        {comments.map((comment,index) => {
                                                            const lastComment = comments.length -1 === index
                                                            return(
                                                            <Fade up key={comment._id}>
                                                                <div ref={lastComment ? setRef : null} className={comment.name === cmName ? "cmSec fromMe " :"cmSec  fromAt"}  key={comment._id} style={{ overflow: "hidden" }}>
                                                                    <h6 className={props.darkMode ? "text-white"  :"text-white"}>
                                                                     
                                                                        <span className="cmSpan">{comment.name}{' '}</span> : {''}
                                                                        {comment.message}
                                                                    </h6>
                                                                    <h5 className={props.darkMode ? "text-white"  :"text-white"}>
                                                                        {new Date(comment.createdAt).toLocaleTimeString('uk', { hour12: false })}
                                                                    </h5>

                                                                </div>

                                                            </Fade>
                                                        )})}  </div>  </> : null}


                                        </Card.Text>
                                        <Card.Text>
                                            {!showCom ? <Button variant="info" style={{
                                                marginTop: "5%",
                                                width: "100%",
                                                marginBottom: "2%"
                                            }} onClick={commentHandle}>
                                                Add a New Comment
                        </Button> : null}

                                            {showCom ?
                                                <>
                                                    <form onSubmit={submitComment}>
                                                        <div className="form-group">
                                                            <input type="text" defaultValue={cmName} name="cmName" className="cmName" id="name" hidden />
                                                        </div>
                                                        <div className="form-group">
                                                            <div className="input-group" >
                                                                <textarea className="form-control" type="textarea" name="message" id="message" rows="3" className={props.darkMode ? "form-control darkIn" : "form-control"} placeholder="Comment..." value={message} onChange={commentInput} />
                                                                <div className="input-group-append">
                                                                    <button className="btn btn-dark" type="submit" >
                                                                        <BiSend />
                                                                    </button>
                                                                </div>

                                                            </div>

                                                        </div>
                                                    </form>

                                                </>
                                                : null}

                                        </Card.Text>
                                    </Card.Body>
                                    <Card.Footer className="card-footer">
                                        {date}
                                        {localStorage.getItem('userId') === id ? <Card.Text>
                                            <Button variant="danger" style={{
                                                marginTop: "5%",
                                                width: "100%"
                                            }} onClick={deletePost}>
                                                Delete Post
                        </Button>
                                        </Card.Text> : null}

                                    </Card.Footer>
                                </Card>

                            </Fade>

                    }
                    {modal ?
                        <EditModal darkMode={props.darkMode} update={editValue} token={tokenValue} updatePost={updatePost} title={title} content={content} setTitle={setTitle} setContent={setContent} image={image} handleModal={handleModal} />

                        : null}
                </Container>
            </Container>
        </SocialProvider>
    );
}

export default SinglePost;