import React, { useState, useRef, useContext, useEffect } from 'react'
import { Card, Button } from "react-bootstrap";
import { SocialContext, SocialProvider } from '../../context/socialContext';
import { Link } from "react-router-dom"
import { toast } from 'react-toastify';
import SimpleReactValidator from 'simple-react-validator';
import { isEmpty } from "lodash"
const Modal = ({ handleModal, user, tokenValue , darkMode }) => {
    const { post, nameData , edit , editPost } = useContext(SocialContext);
    const [postValue, setPostValue] = post;
    const [nameValue, setNameValue] = nameData;
    const [editValue, setEditValue] = edit;
    const [editPostValue, setEditPostValue] = editPost;
    const [name, setName] = useState({})
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [image, setImage] = useState("");
    const [, forceUpdate] = useState();

    const validator = useRef(
        new SimpleReactValidator({
            messages: {
                required: "You Should Fill All the Fields",
                min: "5 Character at Least",

            },
            element: message => <div style={{
                background: "#c54848",

                padding: "10px 10px",
                color: "white",
                marginTop: "2%",
                borderRadius: "5px"
            }}>

                {message}

            </div>
        })
    )
    const titleChange = (e) => {
        setTitle(e.target.value)
        validator.current.showMessageFor("title")


    }
    const contentChange = (e) => {
        setContent(e.target.value)
        validator.current.showMessageFor("content")


    }


    const submitPost = postData => {
        setEditValue(true)
        const formData = new FormData();
        formData.append('title', title)
        formData.append('content', content)
        formData.append('image', postData.target.image.files[0])
        let url = 'https://api.maniyeganeh.ir/feed/post';
        let method = "POST";
        fetch(url, {
            method: method,
            body: formData,
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('Your_token')
            }
        })
            .then(res => {
                if (validator.current.allValid()) {
                    if (res.status !== 200 && res.status !== 201) {
                        throw new Error('Creating or editing a post failed!');

                    }
                } else {
                    validator.current.showMessages();
                }
                toast.dark("Uploading" , {
                    position:"top-right"
                })
                return res.json();
            })
            .then(resData => {
               
              
                setNameValue(resData.creator)
                toast.success(`${resData.message}`, {
                    position: "top-center"
                })
                const post = {
                    _id: resData.post._id,
                    title: resData.post.title,
                    content: resData.post.content,
                    image: resData.post.image,
                    creator: resData.creator,
                    createdAt: resData.post.createdAt,
                    name:resData.creator.name


                };
            
         
                setPostValue(post)






            })
            .catch(err => {
                console.log(err);
                toast.error("Can't Reach The Server", {
                    position: "top-right"
                })
              
            });
        handleModal();

        forceUpdate(1);
    }
 
   
    return (

        <SocialProvider>

            <Card className={darkMode ? "cardModal darkMod" : "cardModal" }>
                {localStorage.getItem('Your_token') ?
                    <Card.Body>
                        <Card.Title>
                            Create a Post
             </Card.Title>
                        <form onSubmit={submitPost}>
                            <div className="form-group">
                                <label htmlFor="title">Title Of Your Post</label>
                                <input type="text" id="title" name="title" className={darkMode ? "form-control darkIn" : "form-control"} value={title} onChange={titleChange} />
                                {validator.current.message(
                                    "title",
                                    title,
                                    'required|min:5'
                                )}
                            </div>
                            <div className="form-group">
                                <label htmlFor="content">Content of Your Post</label>
                                <textarea type="textarea" name="content" id="content" rows="3" className={darkMode ? "form-control darkIn" : "form-control"} value={content} onChange={contentChange} />
                                {validator.current.message(
                                    "content",
                                    content,
                                    "required|min:5"
                                )}
                            </div>
                            <div className="form-group " className="file">
                            <label className="form-check-label" htmlFor="image">Your Image <span>(JPEG , JPG , PNG)</span> </label>
                                    
                                <input type="file" id="image" name="image" className="form-check-input" />
                              
                            </div>
                            <div className="sub">
                                <button type="submit" className="btn btn-success">Submit</button>

                            </div>
                        </form>
                    </Card.Body>
                    :
                    <Card.Body>
                        <Card.Title>
                            Please Sign In
             </Card.Title>
                        <Link to="/login">
                            <Button className="btn btn-success">
                                Sign In
             </Button>
                        </Link>
                    </Card.Body>

                }

            </Card>
        </SocialProvider>
    );
}

export default Modal;