import React, { useState, useEffect, useContext , useCallback } from 'react';
import { Container, Row, Card, Col , Button } from "react-bootstrap";
import Helmet from "react-helmet"
import { BiPlusCircle } from "react-icons/bi";
import Bar from "../Feed/Bar";
import { Fade } from "react-reveal";
import { Link } from "react-router-dom"
import "../Feed/Feed.css";
import Modal from './Modal';

import Face from "../../img/face.png";
import Profile from './Profile';
import { SocialContext, SocialProvider } from '../../context/socialContext';

import { WiDaySunny, WiMoonAltThirdQuarter } from "react-icons/wi";
import { toast } from 'react-toastify';
const Feed = ({ history, darkMode, darkModeHandle }) => {
    const { user, token, post, auth, nameData } = useContext(SocialContext);
    const [postValue, setPostValue] = post;
    const [nameValue] = nameData;
    const [userValue] = user;
  
    const [tokenValue] = token;
    const [modal, setModal] = useState(false);
  



    const handleModal = () => {
        setModal(!modal);

    }
    // useCallback(() => {
    //     if(localStorage.getItem('Your_token')){
    //         toast.dark("Welcome Back" , {
    //             position:"bottom-center"
    //         })
    //     }
    // },[])
    useEffect(() => {





        fetch('https://api.maniyeganeh.ir/feed/posts', {
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('Your_token')
            }
        })
            .then(res => {
                return res.json()

            }).then(resData => {









                
                


                // setNameValue(resData.creator.name)
                setPostValue(resData.posts);

      

            })
            .catch(err => {
                console.log(err);

            })

        // const socket = openSocket('http://localhost:8080', { transports: ['websocket'] });


        // socket.on('posts', data => {
        //     if (data.action === "create") {
        //         console.log(data);


        //     }

        // })












    }, [postValue])

    
    const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: "smooth" })
    }

    return (
        <SocialProvider>
            <Helmet>
                <title>
                    Social | Home
                </title>
            </Helmet>

            <Container fluid className={darkMode ? "dark" : "light"} style={{
                height: "auto",
                paddingBottom: "150px",
                transition: "all 0.5s"

            }}>
                <Bar handleModal={handleModal} modal={modal} darkMode={darkMode} />
                <Row>
                    <Col md={3} xs={12} sm={12}>

                        <div className={modal ? "add-btn open" : "add-btn"}>
                            <div className="btn" type="button" onClick={handleModal} style={{
                                outline: "none"
                            }}>
                                <BiPlusCircle />
                            </div>

                        </div>

                        {
                            modal ?

                                <Modal handleModal={handleModal} name={nameValue} user={user} tokenValue={tokenValue} darkMode={darkMode}/>


                                :
                                null
                        }

                    </Col>
                    <Col md={6} xs={12} sm={12} className={modal ? "feed blur" : "feed"}>


                        {
                            postValue.length > 0 ?
                                postValue.map((ps, index) => (
                                    <Fade key={index}>
                                        <Card className={darkMode ? "feedCard darkCard" : "feedCard"} >
                                            <Card.Body>
                                                <Card.Title className="card-title">
                                                    <h3>
                                                        {(ps.creator.profilePic === null || ps.creator.profilePic === " " || ps.creator.profilePic === undefined) ?
                                                            <img src={Face}  className={darkMode ? "proFeed proDark" : "proFeed"} alt="No pic" style={{ width: "40px", height: "40px", borderRadius: "100%" }} />
                                                            :
                                                            <img src={`https://api.maniyeganeh.ir/${ps.creator.profilePic}`} className="proFeed" alt="pro" style={{ width: "40px", height: "40px", borderRadius: "100%" }} />
                                                        }
                                                        {" "}  - {" "}{ps.title}
                                                    </h3>
                                                </Card.Title>
                                                <hr />
                                                {ps.imageUrl === undefined ?
                                                    <h3>
                                                        No Pictures
                                        </h3>
                                                    :
                                                    <div className="feedImg">
                                                        <Card.Img src={"https://api.maniyeganeh.ir/" + ps.imageUrl} />
                                                    </div>
                                                }
                                                <hr />

                                                <Card.Text className={darkMode ? "card-content darkCon" : "card-content"}>
                                                    {(ps.creator.profilePic === null || ps.creator.profilePic === " " || ps.creator.profilePic === undefined) ?
                                                        <img src={Face}  className={darkMode ? "proFeed proDark" : "proFeed"} alt="No pic" style={{ width: "40px", height: "40px", borderRadius: "100%" }} />
                                                        :
                                                        <img src={`https://api.maniyeganeh.ir/${ps.creator.profilePic}`} className="proFeed" alt="pro" style={{ width: "40px", height: "40px", borderRadius: "100%" }} />
                                                    }


                                                    {/* <img src={"https://api.maniyeganeh.ir/" + ps.creator.profilePic} className="proFeed" style={{width:"40px" , height:"40px" , borderRadius:"100%"}}/>  */}
                                                    <Link to={`/user/${ps.creator._id}`} className={darkMode ? "spnLnk darkLnk" : "spnLnk"} onClick={scrollToTop}> <span>{ps.creator.name} </span> </Link> : {" "}


                                                    {ps.content}






                                                    <br />
                                                    <div className="text-center" style={{marginTop:"3%"}}>
                                                    {localStorage.getItem('Your_token') != null ?
                                                    <Link to={`/post/${ps._id}`}>
                                                    <Button  className={darkMode ? "cmDark" : "cmLi"}>
                                                        {ps.comments.length > 1 ? `See All ${ps.comments.length} Comments` : "See All Comments"}
                                        {/* See All {ps.comments.length} Comments */}
                                            </Button>
                                            </Link>: null }
                                            </div>
                                            <br/>
                                                    <small>

                                                        Created At  {" "} : {" "}
                                                        {new Date(ps.createdAt).toLocaleString('uk', { hour12: false })}
                                                    </small>
                                                    {localStorage.getItem('Your_token') != null ?
                                                        <button className="btn btn-info">
                                                            <Link to={`/post/${ps._id}`} style={{
                                                                color: "white",
                                                                width: "100%"
                                                            }} onClick={scrollToTop}>
                                                                Go To Post
                                                           </Link>

                                                        </button>
                                                        : null
                                                    }

                                                </Card.Text>






                                            </Card.Body>





                                        </Card>
                                    </Fade>
                                ))
                                :
                                <Fade>


                                    <Card className={darkMode ? "feedCard darkCard" : "feedCard"} style={{
                                        marginTop: "52%",
                                        marginBottom:"100%"
                                    }}>
                                        <Card.Body>
                                            <Card.Title style={{
                                                textAlign: "center"
                                            }}>
                                                Create Your First Post
                            <Card.Text>
                                                    <small>
                                                        Click on + Button
                            </small>
                                                </Card.Text>

                                            </Card.Title>
                                        </Card.Body>

                                    </Card>
                                </Fade>
                        }
                    </Col>
                    <Col md={2} xs={12} sm={12} className={modal ? "user blur" : "user user"}>
                        <Card className={darkMode ? "feedCard darkCard" : "feedCard"} >
                            <Card.Body>
                                <Profile  user={userValue} darkMode={darkMode} />
                               </Card.Body>
                               </Card>
                                <div className="toggle-container" onClick={darkModeHandle}>
                                    <span className={darkMode ? "icon darkIco" : "icon"}>
                                        <WiDaySunny className={!darkMode ? "icon lightIco" : "icon"} />
                                    </span>
                                    <span className="toggle">
                                        <input type="checkbox"
                                            checked={darkMode}
                                            onChange={darkModeHandle}
                                            className="checkbox"
                                            id="checkbox"

                                        />
                                    </span>
                                    <span className={darkMode ? "icon darkIco" : "icon"}>
                                        <WiMoonAltThirdQuarter className={darkMode ? "icon darkIco" : "icon moon"} />
                                    </span>

                                </div>
                    </Col>
                </Row>
            </Container>
        </SocialProvider>
                );
            }
            
export default Feed;