import React, { useState, useRef, useContext, useCallback } from 'react'
import { Container, Row, Button, Form } from "react-bootstrap";
import SimpleReactValidator from "simple-react-validator";
import { Link } from "react-router-dom"
import axios from 'axios'
import { toast } from 'react-toastify';
import { SocialContext, SocialProvider } from '../../context/socialContext';
const Signin = ({ history , darkMode }) => {
    const { user, token, auth } = useContext(SocialContext);
    const [userValue, setUserValue] = user;
    const [tokenValue, setTokenValue] = token;
    const [authValue, setAuthValue] = auth;
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('')
    const[rememberMe , setRememberMe] = useState(true)
    const [, forceUpdate] = useState();


    const validator = useRef(
        new SimpleReactValidator({
            messages: {
                required: "You Should Fill All The Fields",
                password: "Password Must be At Least 7 Characters",
                email: "E-Mail Is Not Valid"
            },
            element:
                message => <div style={{
                    background: "#c54848",

                    padding: "10px 10px",
                    color: "white",
                    marginTop: "2%",
                    borderRadius: "5px"
                }}>{message}</div>
        })
    )
    const emailChange = e => {
        setEmail(e.target.value);
        validator.current.showMessageFor("email")

    }
    const passChange = e => {
        setPassword(e.target.value);
        validator.current.showMessageFor("password")

    }
    const login = (event, authData) => {
        event.preventDefault();
        fetch('https://api.maniyeganeh.ir/auth/login', {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                email: email,
                password: password,

            })
        }).then(res => {
            if (res.status === 422) {
                throw new Error('Validation failed.');
            }
            if (res.status !== 200 && res.status !== 201) {
                console.log('Error!');
                throw new Error('Could not authenticate you!');
            }

            return res.json();
        })
            .then(resData => {

              
          
                
                toast.success(`Hello ${resData.loadedUser.name}`, {
                    position: "top-right"
                })

                localStorage.setItem('Your_token', resData.token);
                localStorage.setItem('userId', resData.userId);
                localStorage.setItem('userPic', resData.loadedUser.profilePic);
                localStorage.setItem('rememberMe' , rememberMe)
                localStorage.setItem('user' , rememberMe ? [resData.loadedUser.name] :'')
                const remainingMilliseconds = 60 * 60 * 3000;
                const expiryDate = new Date(
                    new Date().getTime() + remainingMilliseconds
                );
                localStorage.setItem('expiryDate', expiryDate.toISOString());


                setTokenValue(resData.token)
                setUserValue(resData.loadedUser)
                setAuthValue(true)
                forceUpdate(1)
                history.replace("/")

            })
            .catch(err => {
                console.log(err);
                toast.error(`${err} E-Mail Or Password Is Wrong`,{
                    position:"top-right"
                })

            });



    }
    return (
        <SocialProvider>


            <Container fluid className={darkMode ? "reg login darkLog" : "reg login"}>
                <Container>
                    <Row>
                        <div className={darkMode ? "signBox loginBox darkBox" : 'signBox loginBox'}>
                            <Form onSubmit={login}>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label className="label">Email address</Form.Label>
                                    <Form.Control type="email" name="email" placeholder="Enter Your email" className="input" value={email} onChange={emailChange} />
                                    {validator.current.message("email", email, "required|email")}

                                </Form.Group>

                                <Form.Group controlId="formBasicPassword">
                                    <Form.Label className="label">Password</Form.Label>
                                    <Form.Control type="password" name="password" placeholder="Password" className="input" value={password} onChange={passChange} />
                                    {validator.current.message("password", password, "required|min:7")}

                                </Form.Group>
                             
                             
                                <p>
                                    Don't Have An Account?
                            {" "}
                                    <span>
                                        <Link to="/signup">
                                            {" "}
                                            Sign up Now
                                </Link>
                                    </span>
                                </p>
                                <Button className={darkMode ? "darkBt" :""} type="submit" style={{
                                    backgroundColor: "#44bd32",
                                    border: "none"
                                }}>
                                    Login
</Button>
                            </Form>
                        </div>
                    </Row>
                </Container>
            </Container>
        </SocialProvider>
    );
}

export default Signin;