import React, { useEffect, useContext, useState } from 'react';
import { Container } from "react-bootstrap";
import { Link } from "react-router-dom"
import "../Nav/Menu.css";
import Hamburger from './Hamburger';
import Search from './Search';
import Logo from "./img/logo.png"
import { SocialContext, SocialProvider } from '../../context/socialContext';
const Menu = ({ darkModeHandle, darkMode }) => {
    const { user, auth, token } = useContext(SocialContext);
    const [userValue, setUserValue] = user;
    const [tokenValue, setTokenValue] = token;
    const [authValue, setAuthValue] = auth;
    const [, forceUpdate] = useState();


    const logoutHandler = () => {
        setAuthValue(false);
        setTokenValue(null);
        localStorage.removeItem('Your_token');
        localStorage.removeItem('expiryDate');
        localStorage.removeItem('userId');
        localStorage.removeItem('user');
        localStorage.removeItem('rememberMe');
        localStorage.removeItem('userPic');
        window.location.reload();
        forceUpdate(2)

    }
    return (
        <SocialProvider>


            <Container fluid className={darkMode ? "bar drkB" : "bar"}>



                <div className="row">
                    <div className="menuBox">
                        <div className="img">
                            <Link to="/">
                                <img src={Logo} className="logo" alt="logo" />

                            </Link>
                        </div>
                    </div>
                    <div className="menuBox"></div>
                    <div className="menuBox"></div>
                    <div className="menuBox"></div>
                    <div className="menuBox"></div>
                    <div className="menuBox"></div>
                    <div className="menuBox searchBox">
                        <Search />
                    </div>
                    <div className="menuBox"></div>
                    <div className="menuBox"></div>
                    <div className="menuBox ham">
                        <Hamburger user={userValue} token={tokenValue} auth={authValue} logoutHandler={logoutHandler} darkModeHandle={darkModeHandle} darkMode={darkMode} />
                    </div>
                </div>



            </Container>
        </SocialProvider>
    );
}

export default Menu;