import React, { useRef, useState, useEffect } from 'react'
import { Container, Row, Form, Button } from "react-bootstrap";
import SimpleReactValidator from 'simple-react-validator';
import "./reg.css";
import axios from 'axios';
const Signup = ({ history, darkMode }) => {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const validator = useRef(
        new SimpleReactValidator({
            messages: {
                required: "You Should Fill All The Fields",
                name: 'Must Be At Least 3 Characters',
                password: "Password Must be At Least 7 Characters",
                email: "E-Mail Is Not Valid"
            },
            element:
                message => <div style={{
                    background: "#c54848",

                    padding: "10px 10px",
                    color: "white",
                    marginTop: "2%",
                    borderRadius: "5px"
                }}>

                    {message}

                </div>
        })
    )
    const nameInputChange = (e) => {
        setName(e.target.value)
        validator.current.showMessageFor("name")
    }
    const emailInputChange = (e) => {
        setEmail(e.target.value)
        validator.current.showMessageFor("email")
    }
    const passInputChange = (e) => {
        setPassword(e.target.value)
        validator.current.showMessageFor("password")
    }
    const submitUser = (event) => {
        event.preventDefault();
        const user = {
            name,
            email,
            password
        }
        fetch('https://api.maniyeganeh.ir/auth/signup', {
            method: "PUT",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(user)
        })
            .then(res => {
                if (res.status === 422) {
                    throw new Error(
                        "Validation failed. Make sure the email address isn't used yet!"
                    );
                }
                if (res.status !== 200 && res.status !== 201) {
                    console.log('Error!');
                    throw new Error('Creating a user failed!');
                }
                return res.json();
            })
            .then(resData => {
                console.log(resData);
                history.push("/login");

            })
            .catch(err => {
                console.log(err);

            });
    }
    return (
        <Container fluid className={darkMode ? "reg darkLog" : "reg"}>
            <Container>
                <Row>
                    <div className={darkMode ? "signBox  darkBox" : 'signBox'}>
                        <Form onSubmit={submitUser}>
                            <Form.Group controlId="formBasicName">
                                <Form.Label className="label">Full Name</Form.Label>
                                <Form.Control type="text" name="name" placeholder="Your Full Name" className={darkMode ? "input darkInp" : "input"} value={name} onChange={nameInputChange} />
                                {validator.current.message(
                                    "name",
                                    name,
                                    "required|min:3"
                                )}

                            </Form.Group>
                            <Form.Group controlId="formBasicEmail">
                                <Form.Label className="label">Email address</Form.Label>
                                <Form.Control type="email" name="email" placeholder="Enter Your email" className={darkMode ? "input darkInp" : "input"} value={email} onChange={emailInputChange} />
                                {validator.current.message(
                                    "email",
                                    email,
                                    "required|email"
                                )}
                                <Form.Text className="text-muted">
                                    We'll never share your email with anyone else.
    </Form.Text>
                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">
                                <Form.Label className="label" >Password</Form.Label>
                                <Form.Control type="password" name="password" placeholder="Password" className={darkMode ? "input darkInp" : "input"} value={password} onChange={passInputChange} />
                                {validator.current.message(
                                    "password",
                                    password,
                                    "required|min:7"
                                )}
                            </Form.Group>

                            <Button variant="danger" type="submit">
                                Sign Up!
  </Button>
                        </Form>
                    </div>
                </Row>
            </Container>
        </Container>
    );
}

export default Signup;