import React, { Fragment, useEffect } from 'react';
import { Link } from "react-router-dom"
import { Card } from "react-bootstrap"
import Face from "../../img/face.png";

const Profile = ({ user, darkMode }) => {

 
    return (
        <Fragment>
            {(localStorage.getItem('Your_token')!= null) ? <div className={darkMode ? "user-sec darkSec" : "user-sec"}>
                <Link to={`/user/${localStorage.getItem('userId')}`}>
                    {(localStorage.getItem('userPic') === null || localStorage.getItem('userPic') === " " || localStorage.getItem('userPic') === undefined) ?
                        <img src={Face} alt="face" className={darkMode ? "proFeed proDark" : "proFeed"} alt="No pic" style={{ width: "40px", height: "40px", borderRadius: "100%" }} />
                    :
                    <img src={`https://api.maniyeganeh.ir/${localStorage.getItem('userPic')}`} style={{ width: "50px", height: "50px", borderRadius: "100%", marginBottom: "2%" }} />

                }
                    <h5>

                        {" "}
                        {localStorage.getItem('user')}
                        {/* {user.name} */}

                    </h5>
                </Link>



            </div> :
                <div className={darkMode ? "user-sec darkSec" : "user-sec"}>
                    <Link to="/login">
                        <button className={darkMode ? "btn btn-dark" : "btn btn-info"}>
                            Login
                </button>

                    </Link>
                </div>
            }


        </Fragment>
    );
}

export default Profile;